@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

*:focus {
  outline: 0;
}

html {
  font-family: franklin-gothic-compressed, sans-serif;
  font-style: normal;
  font-weight: 400;
}

h1{
  text-align: center;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.state-map{
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;
}
.state-map svg{
    max-width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    resize: both;
}

.state-map path.selected{
    fill: #e26f27;
    /* fill: #1ca2ab; */
    /* fill: #17848b; */
}

.temp-msg__stateName{
    font-size: 16px;
    line-height: 25px;
    font-weight: 500;
    letter-spacing: 1px;
}

.hoverMsg__extraInfo{
    font-size: 11px;
    line-height: 15px;
    padding-top: 3px;
    font-weight: 300;
}

.hoverMsg{
    position: absolute;
    pointer-events: none;
    display: none;
    background-color: #4a2e1c;
    border: 2px solid white;
    color: white;
    margin: 10px;
    padding: 10px;
    text-align: center;
}
.hoverMsg.hide{
    display: none;
}

.line-pattern{
    opacity: 0.65;
}


@media screen and (min-width: 700px)  {
    .hoverMsg{
        display: block;
    }
}
.legend-wrapper{
    padding: 0px 10px;
    width:100vw;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    height: 65px;
}

.legend-totalAcre{
    /* padding: 5px; */
    height: 65px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.legend-totalAcre__label{
 font-size: 10px;
 font-weight: bold;
}

.legend-totalAcre__number{
    font-family: ff-tisa-web-pro, serif;
    font-style: normal;
    font-weight: 900;
    /* background-color: #4a2e1c; */
    /* padding-top: 5px;
    padding-bottom: 6px; */
    color: #988c83;
    text-align: center;
    font-size: 32px;
    letter-spacing: 5px;
    /* justify-content: center; */
}



/* -- legend color information -- */

.legend-colorInfo{
    padding-left: 20px;
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: space-between;
    height: 65px;
}

.display-flex{
    display: flex;
    align-items: center;
}

.legend-colorInfo__label{
    font-size: 10px;
    font-weight: bold;
    padding-left: 5px;
}

.legend-colorInfo__box{
    width: 13px;
    height: 13px;
}

.legend-colorInfo__color__hasAcres{
    background-color:#988c83;
}

.legend-colorInfo__box__selected{
    background-color: #e26f27;
    /* background-color: #1ca2ab; */
    /* background-color: #17848b; */
}

.legend-colorInfo__color__noAcres{
    background-color: #c4c0bc;
}

@media only screen and (max-width: 480px) {
    .legend-colorInfo{
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        height: 50px;
    }
    .legend-colorInfo__box{
        width: 13px;
        height: 13px;
    }
    .legend-totalAcre__number{
        letter-spacing: 4px;;
    }
    .legend-colorInfo{
        padding-left: 10px;
    }

}
.stateData-table__wrapper{
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}
.stateData-table__th{
    text-align: left;
    padding: 2px 10px;
}
.stateData-table__td{
    text-align: left;
    padding: 2px 10px;
}
.StateDropdownBtn-wrapper{
    width: 100%;
    display: flex;
    flex-direction: column;
    padding-top: 10px;
    align-items: center;
    margin-bottom: 20px;
    margin-top: 10px;
}
select{
    /* appearance: none; */
    display: flex;
    flex-direction: row;
    background-color: #e26f27;
    /* background-color: #1ca2ab; */
    /* background-color: #17848b; */
    border: none;
    padding: 20px 10px;
    border-radius: 16px;
    cursor: inherit;
    color: white;
    font-size: 18px;
    font-weight: 600;
    text-align: center !important;
    align-items: center;
    justify-content: space-around;
    width: 300px;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
}
select:after {
    content: '\2304';
    font-size: 30px;
    line-height: 23px;
    padding-right: 2px;
  }
